import React, { useState } from 'react';
import Title from '../../common/Title';
import './styles.css';

import rubin_selection_slides from '../../../pdf/ciie_rubin_selection.pdf';

const Learning = () => {
  const [abstractIsVisible, setAbstractIsVisible] = useState(false);
  return (
    <div id="learning">
      <Title> Learning </Title>
      <span> Causal Inference and Impact Evaluation </span>
      <div>
        <p> Rubin Causal Model and Selection <a href={rubin_selection_slides} download>[Slides (.pdf)]</a> {' '}  </p>
      <div>
        <span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Topics{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        {abstractIsVisible ? "How do economists think about counterfactuals? What are the consequences of self-selection for identification of causal effects?" : ''}
      </div>
        <p> "Canonical" Regression Discontinuity Designs  </p>
        <div>
        <span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Topics{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        {abstractIsVisible ? "" : ''}
      </div>
        <p> Roy Models, Marginal and Policy Relevant Treatment Effects {' '} </p>
        <div>
        <span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Topics{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        {abstractIsVisible ? "" : ''}
      </div>
      </div> 
      <span> Economics and Policy of Human Capital Formation </span>
      <div>
        {/* <p> Canonical Concepts  <a href={rubin_selection_slides} download>[Annotated Bibliography - Division of Labor (.pdf)]</a> {' '}  <a href={rubin_selection_slides} download>[Annotated Bibliography - Skills and Tasks (.pdf)]</a> {' '} </p> */}
        <p> Teachers  <a href={rubin_selection_slides} download>[Annotated Bibliography (.pdf)]</a> {' '} </p>
      </div>
    </div> 
    
  )

}
  
export default Learning;
